.App {
  width: 100%;
}

.divider {
  width: 4rem;
  height: 0.4rem;
  background-image: linear-gradient(to right, #36f, #c3f);
  border-radius: 0.4rem;
}

.title {
  font-size: 3rem;
  background-image: linear-gradient(to right, white, #595959);
    background-clip: text;
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.aboutme {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.aboutme img {
  width: 12rem;
  height: 12rem;
  border-radius: 50%;
}

.subheading {
  margin-left: 2rem;
  font-size: 2rem;
}

@media screen and (max-width: 768px) {
  .divider {
    margin-left: 1rem;
  }
  .title, p {
    margin-left: 1rem;
  }
  .aboutme {
    display: flex;
    flex-direction: column-reverse;
  }
}