.header {
    width: 100%;
    max-width: 1080px;
    position: fixed;
    background-color: rgba(0, 0, 26, 0.8);
    backdrop-filter: blur(1rem);
    z-index: 9;
}
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-block: 0.4rem;
}

.portfolio {
    font-size: 2rem;
}

.options {
    display: flex;
    justify-content: space-evenly;
}

.link {
    cursor: pointer;
    margin-inline: 0.8rem;
    font-size: 1.2rem;
    color: #939393;
}

.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.toggle {
    display: none;
}

@media screen and (max-width: 768px) {
    .toggle {
        display: block;
        margin:0;
    }
    .navbar {
        margin-inline: 2rem;
        display: flex;
        justify-content: space-between;
    }
    .options {
        display: none;
    }
}