.home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
}

.main {
    height: 100%;
    background-image: linear-gradient(to right, white, #595959);
    background-clip: text;
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.left h1 {
    font-size: 6rem;
    margin-block: 1rem;
}

.left h4 {
    margin-block: 1rem;
    font-size: 3rem;
}

.button {
    border: none;
    background-image: linear-gradient(to right, #36f, #c0c);
    border-radius: 2rem;
    padding-block: 0.6rem;
    color: white;
    padding-inline: 0.6rem;
    box-shadow: 0.05rem 0.05rem 10rem white;
    outline:none;
    cursor: pointer;
}

.right {
    width: 60%;
}

@media screen and (max-width: 768px) {
    .home {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .right {
        width: 100%;
    }
}
