.about{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow-x: scroll;
    margin-block: 2rem;
    padding-left: 2rem;
    height: 35vh;
}

.skill {
    padding: 2rem;
    height: 8rem;
    width: 8rem;
    margin-right: 2rem;
    text-align: center;
    border-radius: 0.4rem;
    border: 1px solid rgba(255, 255, 255, 0.8);
    box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.1);
}

.skill:hover {
    transform: scale(1.2);
    background-color: #33f;
    z-index: 4;
}

.logo {
    font-size: 6rem;
}