.projects {
    display: flex;
    justify-content: space-evenly;
    margin-block: 2rem;
    flex-wrap: wrap;
}

.media {
    height: 10rem;
    width: 100%;
    border-top-right-radius:0.4rem;
    border-top-left-radius: 0.4rem;
}

.projectbody {
    padding-bottom: 1rem;
    text-align: center;
}

.stack {
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 1rem;
}

.project {
    width: 30%;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 0.4rem;
    margin-block: 1rem;
    box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.1);
}

.big {
    font-size: 2rem;
}

.smalldivider {
    height: 0.2rem;
    width: 4rem;
    border-radius: 0.4rem;
    background-image: linear-gradient(to right, #ff6, #f60);
    margin: 0 auto;
}

.light {
    color: #999;    
}

.highlight {
    color: white;
}

.srcbutton {
    background-color: #4d4dff;
    border-radius: 0.4rem;
    padding-inline: 1rem;
    border: none;
    outline: none;
}

@media screen and (max-width: 768px) {
    .projects {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
    }
    .project {
        width: 90%;
    }
}