.education {
    margin-block: 4rem;
    margin-left: 2rem;
    height: 110vh;
}

.education::after {
    content: '';
    top: 280%;
    left: 20%;
    width: 60%;
    height: 70%;
    position:absolute;
    background: url('../../assets/study.svg');
    background-size: contain;
    background-repeat: no-repeat;
    z-index: -1;
}

.box1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-block: 1rem;
}

.mediumtext {
    font-size: 1.4rem;
}

.info {
    width: 60%;
    border-radius: 0.4rem;
    border: 1px solid rgba(255, 255, 255, 0.5);
    padding: 2rem;
    background-color: rgba(99, 33, 255, 0.3);
    backdrop-filter: blur(20px);
    box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.1);
}

.place {
    text-align: right;
}

@media screen and (max-width: 768px) {

    .box1 {
        flex-direction: column;
    }

    .education {
        margin-left: 0;
    }
    .education::after {
        content: '';
        top: 420%;
        left: 5%;
    }
}